.looking-for-drivers {
  margin: auto;
  text-align: center;
  padding-top: 100px;
  width: 350px;
  .picture {
    width: 100px;
    height: 100px;
    border-radius: 100px;
    position: absolute;
    background-color: white;
    top: 205px;
    left: 0;
    right: 0;
    margin: auto;
  }
  .driver-info {
    padding: 60px 60px 40px 60px;
    margin-top: 60px;
    margin-bottom: 20px;
    .info-content {
      padding-top: 40px;
    }
    .clickable {
      margin-top: 30px;
    }
  }
}
